'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-read-only-property';


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name dcusum
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/blas/ext/base/ndarray/dcusum}
*/
import dcusum from '@stdlib/blas-ext-base-ndarray-dcusum';
setReadOnly( ns, 'dcusum', dcusum );

/**
* @name dsum
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/blas/ext/base/ndarray/dsum}
*/
import dsum from '@stdlib/blas-ext-base-ndarray-dsum';
setReadOnly( ns, 'dsum', dsum );

/**
* @name gcusum
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/blas/ext/base/ndarray/gcusum}
*/
import gcusum from '@stdlib/blas-ext-base-ndarray-gcusum';
setReadOnly( ns, 'gcusum', gcusum );

/**
* @name gsum
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/blas/ext/base/ndarray/gsum}
*/
import gsum from '@stdlib/blas-ext-base-ndarray-gsum';
setReadOnly( ns, 'gsum', gsum );

/**
* @name scusum
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/blas/ext/base/ndarray/scusum}
*/
import scusum from '@stdlib/blas-ext-base-ndarray-scusum';
setReadOnly( ns, 'scusum', scusum );

/**
* @name ssum
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/blas/ext/base/ndarray/ssum}
*/
import ssum from '@stdlib/blas-ext-base-ndarray-ssum';
setReadOnly( ns, 'ssum', ssum );


// EXPORTS //

export default ns;
